import wx from 'weixin-js-sdk'
const wxRouter = {
	go: (url) => {
		return new Promise((resolve, reject) => {
			wx.miniProgram.navigateTo({
				url: url,
				success: () => {
					resolve(url)
				},
				fail: () => {
					reject(new Error(url))
				}
			})
		})
	},
	back: (num = 1) => {
		wx.miniProgram.navigateBack({
			delta: num
		})
	},
	gotab: (url) => {
		return new Promise((resolve, reject) => {
			wx.miniProgram.switchTab({
				url: url,
				success: () => {
					resolve(url)
				},
				fail: () => {
					reject(new Error(url))
				}
			})
		})
	},
	replace: (url) => {
		return new Promise((resolve, reject) => {
			wx.miniProgram.redirectTo({
				url: url,
				success: () => {
					resolve(url)
				},
				fail: () => {
					reject(new Error(url))
				}
			})
		})
	}
}
module.exports = {
	wxRouter
}



