import Vue from 'vue'
import Vuex from 'vuex'
import App from './App'
import VueRouter from 'vue-router'
import Router from './router'
import state from './store/state'
import mutations from './store/mutations'
import getters from './store/getters'
import storeAction from './store/actions'
import axios from 'axios';
import wx from 'weixin-js-sdk'
import Mint from 'mint-ui';
import 'mint-ui/lib/style.css';

Vue.use(Mint);
Vue.use(Vuex)
Vue.use(VueRouter)
let ua = window.navigator.userAgent.toLowerCase()
let isWechat = (!(ua.match(/wechat/i) === null)) || (!(ua.match(/micromessenger/i) === null))
let isIOS = !!(window.navigator && window.navigator.userAgent || '').match(/\(i[^;]+;( U;)? CPU.+Mac OS X/)
Vue.prototype.isWechat = isWechat
Vue.prototype.httpUrl = window.location.origin
Vue.prototype.isIOS = isIOS
Vue.prototype.$http = axios
Vue.prototype.wx = wx
const actions = Object.assign(storeAction, {
  async adv ({ commit, state }, type) {
    if (!state.ADV.get(type)) {
      const adv = await Vue.http.get('/api/adv?type=' + type)
      commit('setAdv', {data: adv.data.data, advIndex: type})
    }
  }
})
// store.commit('show')
let store = new Vuex.Store({
  state,
  mutations,
  getters,
  actions
})

const router = new VueRouter({
  mode: 'history',
  routes: Router
})

router.beforeEach(function (to, from, next) {
	document.title = to.name
  next()
})

router.afterEach(function (to) {
})
Vue.config.productionTip = false

new Vue({
  store,
  router,
  render: h => h(App)
}).$mount('#app-box')


