<style lang="less">
  body {
  }
  *{
		margin: 0;
		padding: 0;
  }
  html, body {
    width: 100%;
    overflow-x: hidden;
		font-size: 0.3rem;
		margin: 0;
		padding: 0;
  }
  .router-view{
    position: absolute;
    width: 100%;
    height: 100%;
    overflow-x: hidden;
    overflow-y: auto;
    top:0;
    left: 0;
  }
</style>

<template>
  <div>
    <router-view class="router-view"></router-view>
  </div>
</template>

<script>
	import {mapState} from 'vuex'
	import { wxRouter } from './utils/util.js'
	// this.$store.commit('setRecommendList', res.body.data)
  export default {
		computed: mapState({
      // tab: state => state.tab
    }),
    data () {
      return {
				selected: '首页',
			}
    },
		watch: {
      selected (newValue) {
        switch (newValue) {
          case '首页':
            this.$router.replace('/homePage')
            break
        }
      },
      '$route' (a, b) {
        if (b.path === '/rewardGoods_copy') {
        }
      }
    },
    created: function () {
      this.uppateFontSize()
      window.onresize = this.uppateFontSize
    },
    methods: {
      uppateFontSize () {
        let windowWidth = window.innerWidth
        document.getElementsByTagName('html')[0].style.fontSize = windowWidth * 100 / 750 + 'px'
      },
			navigate (url) {
				try {
					wxRouter.go(url).catch((err) => {
						wxRouter.go(`../../${url}`)
					})
				} catch (e) {}
			}
    }
  }
</script>




